.contact {
    font-family: "GabiaMaeumgyeol";
    width: 100%;
    animation: fadeIn 0.3s ease-in-out;
}

.contact-banner {
    width: 100%;
    height: 600px;
    font-size: 3rem;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    background-image: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3) );
}

@keyframes fadeIn {
    0% {
        opacity: 0; /* 시작 시 투명도 0 */
    }
    100% {
        opacity: 1; /* 끝나면 투명도 1 */
    }
  }


  .contact-location {
    height: 700px;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    border-bottom: 2px solid #ddd;
  }
  
  .contact-location-leftside {
    width: 40%;
  }
  .contact-location-rightside {
    width: 60%;
  }
  
  .contact-location-title {
    margin-top: 20%;
    margin-left: 10%;
    font-size: 2rem;
    font-weight: bold;
    color: #333;
  }
  
  .contact-location-title-line {
    margin-left: 10%;
    margin-top: 8px;
    height: 2px;
    background-color: #4CAF50;
    width: 40px;
  }
  
  .contact-location-detail {
    margin-top: 50px;
    color: #777;
    font-size: 16px;
    line-height: 1.5;
    margin-left: 10%;
  }
  
  .contact-location-map {
    height: 100%;
  }




  
  .contact-inquiry {
    height: 100vh;
    display: grid;
    grid-template-columns: 4fr 2fr;
    padding-top: 10vh;
  }

  .contact-inquiry-left{
    border: solid white 10px;
    width: 80%;
    height: 80%;
    margin-left: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
  }
  
  .contact-inquiry-left-content {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15vh; /* 각 항목 간의 간격 */
  }
  
  .contact-inquiry-item {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  
  .contact-inquiry-item-title {
    font-weight: bold;
    font-size: 2rem;
  }
  
  .contact-inquiry-item-detail {
    font-size: 1rem;
  }

  .contact-inquiry-right-title{
    font-size: 2.5rem;
    font-weight: bold;
  }

  .contact-inquiry-right-title-line {
    height: 4px;
    background-color: #ff6600;
    width: 3rem;
    margin-left: 120px;
  }

  
  @media screen and (max-width: 768px) {
    .contact-location {
      height: 700px;
      display: grid;
      grid-template-columns: 1fr;
      background-color: #fff;
      border-bottom: 2px solid #ddd;
    }
    .contact-location-leftside {
      width: 100%;
      height: 0px;
    }
    .contact-location-rightside {
      width: 100%;
    }

    .contact-location-map {
      height: 100%;
    }

    .contact-inquiry-left-content {
      width: 90%;
      display: grid;
      grid-template-columns: 1fr;
      gap: 8vh; /* 각 항목 간의 간격 */
    }
  }
  
